import React, { useEffect, useState } from 'react'
import { useDispatch, useSelector } from 'react-redux'
import {
  View,
  Text,
  TouchableOpacity,
  SafeAreaView,
  FlatList,
  ScrollView,
  Picker,
} from 'react-native'
import { Input } from 'react-native-elements'
import { LoadingSplash, Brand, Frame, Button, ContentProp } from '../../../Components'

import { useTheme } from '../../../Theme'
import { useTranslation } from 'react-i18next'
import { navigationRef, isApp } from '../../../Navigators/Root'

import { ErrorHandler } from '../../../Store/Firebase/Utils'
import FirebaseBatch from '../../../Store/Firebase/Batch'
import { DataStringCheck } from '../../../Services/utils/formators'

const Batchs = () => {
  const { t } = useTranslation()
  const { Common, Fonts, Gutters, Layout, Images, Colors } = useTheme()
  const dispatch = useDispatch()

  const firebaseLoading = useSelector(state => state.firebase.loading)
  const firebaseError = useSelector(state => state.firebase.error)

  const farmId = useSelector(state => state.firebase.activeData&&state.firebase.activeData.farm?state.firebase.activeData.farm:undefined)
  const batchDatas = useSelector(state => state.firebase.batchs)

  const [EditView, setEditView] = useState(0)
  const [HideClosed, setHideClosed] = useState(true)
  const [SelectedBatch, setSelectedBatch] = useState("")

  useEffect(() => {
    if (firebaseError && firebaseError.message) dispatch(ErrorHandler.action())
  },[])

  useEffect(() => { //Error if farmid not equal ???... multiple
    if (farmId && (!batchDatas || typeof batchDatas === 'undefined')) {
      dispatch(FirebaseBatch.action({FarmId: farmId}))
    }
  },[farmId])

  if (batchDatas && typeof batchDatas !== 'undefined') {
    return(
      <SafeAreaView style={[Layout.fill, Layout.fullSize]}>
        <ScrollView style={[Gutters.smallVPadding, Gutters.smallHPadding]}>
          {EditView === 0?(
            <View style={[Layout.fill, Layout.fullSize, Layout.colVCenter]}>
              <Button Texts={["New Batch", "Edit or Close Batch"]} Frame={true} Actions={[(ele) => {
                if (ele.index === 0) {
                  setSelectedBatch("")
                  setEditView(1)
                }else {
                  setSelectedBatch(Object.keys(batchDatas).filter(e => e !== 'update_time' && (!batchDatas[e].state || batchDatas[e].state !== "Closed"))[0])
                  setEditView(2)
                }
              }]} />
              <View style={[Layout.fullWidth, Layout.colVCenter, {marginVertical:20, marginBottom:40}]}>
                <Button Texts={Object.keys(batchDatas).filter(e => e !== 'update_time' && (!batchDatas[e].state || batchDatas[e].state !== "Closed"))} Actions={[(ele) => {
                  setSelectedBatch(ele.text)
                  //setEditView(2)
                }]}/>
              </View>
              <Button Texts={[HideClosed?"Hide Closed Batche's":"Show Closed Batche's"]} Actions={[() => {
                setHideClosed(val => !val)
              }]} />
              {!HideClosed&&(
                <View style={[Layout.fullWidth, Layout.colVCenter, {marginVertical:20}]}>
                  <Button Texts={Object.keys(batchDatas).filter(e => e !== 'update_time' && (batchDatas[e].state && batchDatas[e].state === "Closed"))} Actions={[(ele) => {
                    setSelectedBatch(ele.text)
                    //setEditView(2)
                  }]}/>
                </View>
              )}
            </View>
          ):(
            <View style={[Layout.fill, Layout.fullSize, Layout.colVCenter]}>
              {EditView === 1 &&( // Create
                <Input label={"Batch Name"}
                  containerStyle={[Layout.midWidth, {borderWidth:1, marginTop:10}]}
                  style={[Layout.fill, Layout.midWidth, Common.textInput]}
                  onChangeText={text => {
                    if (text){
                      if (DataStringCheck(text)){
                        setSelectedBatch(text)
                      }
                    }else {
                      setSelectedBatch(text)
                    }
                  }}
                  value={
                    SelectedBatch
                  }
                  selectTextOnFocus
                />
              )}
              {EditView === 2&&( // Edit
                <Picker
                  mode="dropdown"
                  selectedValue={SelectedBatch}
                  style={[Layout.midWidth, { height: 50, justifyContent:"flex-end"}]}
                  onValueChange={(itemValue, itemIndex) => {
                    if (batchDatas && batchDatas[itemValue]) {
                      var batch = batchDatas[itemValue]

                      var Pens = []
                      var pens = sortObject(batch.pens)
                      Object.keys(pens).forEach(element => {
                        if (!batch.pens[element].enddate) {
                          if (localDataStore.pens && localDataStore.pens[element]){
                            var _pen = localDataStore.pens[element]
                            var item = {}
                            item.item = element;
                            item.id = _pen.section + "_" + element;
                            Pens.push(item)
                          }
                        }
                      });

                      setSelectedBatch(itemValue)

                      /*this.setState({
                        BatchName: itemValue,
                        StartD: batch.time,
                        Curve: batch.gcurve,
                        DevianceMin: String(batch.datas.devianceMin),
                        DevianceMax: String(batch.datas.devianceMax),
                        avaiblePens: this.state.freePens.concat(Pens),
                        selectedPens: Pens,
                      })*/
                    }
                  }}
                >
                  {Object.keys(batchDatas).filter(e => e !== 'update_time' && (!batchDatas[e].state || batchDatas[e].state !== "Closed")).map((batch, i) => {
                    return (<Picker.Item label={batch} value={batch} key={i}/>)
                  })}
                </Picker>
              )}
              <View style={[Layout.midWidth, Layout.alignItemsCenter, {marginVertical:20}]}>
                <Text>Element not done</Text>
              </View>
              
              <Button Texts={[t('ActionButtons.Save'),t('ActionButtons.Cancel')]} Frame={true} Actions={[ele => {
                if (ele.index === 1) {
                  setEditView(0)
                }
                else { //Save new / edit - batch
                  
                }
              }]} />
            </View>
          )}
        </ScrollView>
      </SafeAreaView>
    )
  } else {
    return (<LoadingSplash />) 
  }
}

export default Batchs
